import Vue from 'vue'
import Router from 'vue-router'
import moment from 'moment'
import 'html5-reset/assets/css/reset.css'
// 承载
import applySuccessList from '../pages/applySuccessList.vue'
// 主页
import applySuccessListIndex from '../views/apply-success-list.vue'

Vue.use(Router)

/*
 date: 传入的日期 , 如时间戳
 format: 格式化类型 , 如'YYYY-MM-DD HH:MM:ss'
 举个栗子:
 {{ time | dateFormat('YYYY-MM-DD') }}
*/

Vue.filter( 'dateFormat' , function(value,type){  //时间格式化过滤
    return moment(value).format(type) ;
});

const router = new Router({
  routes: [{
      path: '/',
      name: 'applySuccessListIndex',
      component: applySuccessListIndex
    }
  ]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#applySuccessList',
  router,
  components: {
    applySuccessList
  },
  template: '<applySuccessList/>'
})



